<template>
    <v-app>
        <!--  -->
        <nav>
            <v-toolbar color="cyan" light>
                <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
                <v-toolbar-title @click="gotoMain" style="color: white; margin-left: 20px; cursor: pointer;">DK-Zone</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-toolbar-items>
                    <v-btn icon @click="gotoSearch"><v-icon>search</v-icon></v-btn>
                    <v-btn icon @click="Logout"><v-icon>exit_to_app</v-icon></v-btn>
                </v-toolbar-items>
            </v-toolbar>
        </nav>
        <!--    -->
        <v-navigation-drawer v-model="drawer" absolute temporary>
            <v-list-item>
                <v-list-item-avatar>
                    <v-img :src=Current_UserInfo.photo></v-img>
                </v-list-item-avatar>
                <v-list-item-content>
                    <v-list-item-title> {{ Current_UserInfo.nick }} </v-list-item-title>
                </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>
            <v-list dense>
                <v-list-item v-for="item in Menuitems" :key="item.title" :to="item.url" link>
                    <v-list-item-title>
                        <v-list-item-title> {{ item.title }} </v-list-item-title>
                    </v-list-item-title>
                </v-list-item>
            </v-list>
            <v-divider></v-divider>
            <v-list dense>
                <v-list-item v-for="item in Menuitems2" :key="item.title" :to="item.url" link>
                    <v-list-item-title>
                        <v-list-item-title> {{ item.title }} </v-list-item-title>
                    </v-list-item-title>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>
        <!--   -->
        <v-container id="navigation">
            <v-layout wrap justify-center>
                <v-flex xs12 sm9 md6>
                    <!--   -->
                    <v-card>
                        <v-tabs>
                            <v-tab>
                                
                            </v-tab>
                            <v-tab-item>
                                <v-form id="modifyform" :class="{ active: this.mode }" style="width: 50%; margin: 50px 25%">
                                    <v-text-field v-model="user.userid" label="" style="margin-top: 15px;"></v-text-field>
                                    <v-text-field @keyup.enter="Modify" type="password" v-model="user.userpw" label="" style="margin-top: 15px;"></v-text-field>
                                    <v-btn @click="Modify" large outlined style="width:40%; margin: 0 30%; margin-bottom: 50px;"> </v-btn>
                                </v-form>
                                <v-form id="remodifyform" :class="{ active: !this.mode }" style="width: 50%; margin: 50px 25%">
                                    <v-text-field v-model="user.userpw" type="password" counter=20 label="" style="margin-top:-15px;"></v-text-field>
                                    <v-text-field v-model="user.userpwck" type="password" counter=20 label=" " style="margin-top:-15px;"></v-text-field>
                                    <v-text-field v-model="user.nick" label="" counter=20 style="margin-top:-15px;"></v-text-field>
                                    <v-text-field v-model="user.email" label="" counter=20 style="margin-top:-15px;"></v-text-field>
                                    <v-text-field @keyup.enter="Remodify" v-model="user.phone" label="" style="margin-top:-15px;"></v-text-field>
                                    <v-btn @click="Remodify" large outlined style="width:40%; margin: 0 30%; margin-bottom: 50px;"></v-btn>
                                </v-form>
                            </v-tab-item>
                            <v-tab>
                                
                            </v-tab>
                            <v-tab-item>
                                <v-form style="width: 50%; margin: 50px 25%">
                                    <div style="width: 100%; padding: 25px; border: 2px solid black; background-color: yellow;">
                                        <h5 style="text-align: center; color: red;">:       , ,   DB  ,     .</h5>
                                    </div>
                                    <v-text-field v-model="user.userid" label="" style="margin-top: 15px;"></v-text-field>
                                    <v-text-field @keyup.enter="Resign" type="password" v-model="user.userpw" label="" style="margin-top: 15px;"></v-text-field>
                                    <v-btn @click="Resign" large outlined style="width:40%; margin: 0 30%; margin-bottom: 50px;"></v-btn>
                                </v-form>
                            </v-tab-item>
                        </v-tabs>
                    </v-card>
                </v-flex>
            </v-layout>
        </v-container>
    </v-app>
</template>

<script>
export default {
    data: function(){
        return{
            Current_UserInfo: {
                photo: "",
                nick: "",
                name: "",
                email: "",
                phone: "",
                userbio: ""
            },
            drawer: null,
            user: {
                userid: '',
                userpw: '',
                userpwck: '',
                nick: '',
                email: '',
                phone: ''
            },
            mode: false,
            Menuitems: [
                {title: ' ', url:'/lists'},
                {title: ' ', url:'/issue'},
                {title: ' ', url:'/place'},
            ],
            Menuitems2: [
                {title: ' ', url:'/profile'},
                {title: ' ', url:'/scrap'},
                {title: '', url:'/msgbox'},
                {title: '', url:'/setting'}
            ],
        }
    },
    methods: {
        //eslint-disable-next-line
        Logout: function (event) {
            this.$http.get('/users/logout')
            .then((res) => {
                if(res.data.success == 1){
                    alert(res.data.message);
                    this.$router.push('/');
                }
            })
            .catch(function (err) {
                alert(err);
            })
        },
        LoadProfile: function () {
            let self = this;
            this.$http.get('/profile/load')
            .then((res) => {
                if(res.data.success == 1){
                    self.Current_UserInfo.photo = res.data.photo;
                    self.Current_UserInfo.nick = res.data.nick;
                    self.Current_UserInfo.name = res.data.name;
                    self.Current_UserInfo.email = res.data.email;
                    self.Current_UserInfo.phone = res.data.phone;
                    self.Current_UserInfo.userbio = res.data.userbio;
                    return;
                }
                else{
                    alert(res.data.message);
                    this.$router.push('/lists');
                }
            })
            .catch(function (err) {
                alert(err);
            });
        },
        Modify: function () {
            let self = this;    //this.$http.post    this   .
            //  data  method   
            this.$http.post('/users/modify',{
                user: this.user
            })
            .then(function (res) {
                if(res.data.success == 1){
                    // self   
                    // res.data     ..
                    // json   { result: {id: ~~, pw: ~~} }     .
                    //        json   .
                    self.Changemode(res.data.pw,res.data.nick,res.data.email,res.data.phone);
                }
                else{
                    alert(res.data.message);
                    this.$router.push('/setting');
                }
            })
            .catch(function (err) {
                alert(err);
            })
        },
        Changemode(pw,n,e,p){
            //  
            this.user.userpw = pw;
            this.user.userpwck = pw;
            this.user.nick = n;
            this.user.email = e;
            this.user.phone = p;
            this.mode = true;
        },
        Remodify: function () {
            let self = this;
            this.$http.post('/users/remodify',{
                user: this.user
            })
            .then((res) => {
                if(res.data.success == 1){
                    alert(res.data.message);
                    //     
                    self.mode = false;  //this    data   methods    .
                    //user   
                    self.user.userid = '';
                    self.user.userpw = '';
                    self.user.userpwck = '';
                    self.user.nick = '';
                    self.user.email = '';
                    self.user.phone = '';
                    this.$router.push('/setting');
                }
                else{
                    alert(res.data.message);
                    this.$router.push('/setting');
                }
            })
            .catch(function (err) {
                alert(err);
            })
        },
        Resign: function () {
            this.$http.post('/users/resign',{
                user: this.user
            })
            .then((res) => {
                if(res.data.success == 1){
                    alert(res.data.message);
                    this.$router.push('/');
                }
                else{
                    alert(res.data.message);
                    this.$router.push('/setting');
                }
            })
            .catch(function (err) {
                alert(err);
            })
        },
        gotoMain: function(){
            this.$router.push('/lists');
        },
        gotoSearch: function (){
            this.$router.push('/search');
        }
    },
    beforeMount() {
        this.LoadProfile();
    }
}
</script>

<style scoped>
    .active{
        display: none;
    }
</style>